import helper from '../../common/helper.js'
export default {
	data() {
		return {
			PlanArray: '',
			UserNames: '',
			ptid: '',
			choosedaylist: [],
			index: 0,
			Plans: '',
			userdata: '',
			img: '',
			imgs: [],
			grades: '',
			usernames_box: '',
			imgalist: ["http://lx97.cn/static/mt.png", "http://lx97.cn/static/dz.png"],
			imgalists: [],
			state: ''
		};
	},
	onLoad(e) {

		this.ptid = e.ptid;
		// 获取后台添加的平台
		uni.request({
			url: helper.websiteUrl + '/user/getplantype',
			method: 'GET',
			data: {},
			success: res => {
				if (res.data.code == 200) {
					this.choosedaylist = res.data.data.plans;
				}
			}
		});
	},
	methods: {
		previewImage: function(e) {
			this.imgalists.splice(0, this.imgalists.length);
			this.imgalists.push(e.target.dataset.src);
			wx.previewImage({
				urls: this.imgalists
			})

		},
		openimg: function() {
			wx.previewImage({
				urls: this.imgs
			})

		},


		setData: function(obj) {
			let that = this;
			Object.keys(obj).forEach(function(key) {
				that.$set(that.$data, key, obj[key]);
			});
		},
		// 获取用户上传截图
		getImage: function() {
			var User = this.choosedaylist;
			var index = this.index;
			var _this = this;
			uni.chooseImage({
				success: function(res) {
					_this.setData({
						img: res.tempFilePaths[0],
					});
					uni.showLoading({
						title: '识别中'
					});
					// console.log(res.tempFilePaths[0]);
					uni.uploadFile({
						url: helper.websiteUrl + '/annexes/uploadimgbyplan',
						filePath: res.tempFilePaths[0],
						fileType: "uploadFile",
						name: 'upload',
						formData: helper.postdata({
							plantype: User[index]
						}),
						success: function(resup) {
							var resdata = JSON.parse(resup.data);
							if (resdata.code == 200) {
								var url = 'https://cszx.yiruit.net';
								var imgurl = url + resdata.data.path;
								_this.img = imgurl;
								_this.imgs.push(imgurl);
								_this.grades = resdata.data.baseinfo.grade;
								var usernames_boxs = resdata.data.baseinfo.username;
								_this.UserNames = usernames_boxs.replace(/\s*/g, "");
								uni.showToast({
									icon: 'none',
									title: '识别成功！'
								});
								if (_this.UserNames == '' || _this.grades == '0') {
									_this.state = '1';
								} else {
									_this.state = '2';
								}
							} else {
								uni.showToast({
									icon: 'none',
									title: resdata.info,
								});
							}
							uni.hideLoading();
						},
						fail() {
							uni.hideLoading();
							uni.showToast({
								icon: 'none',
								title: '识别失败'
							});
						}
					});
				},
			})
		},
		// 平台选择
		bindPickerChange: function(e) {
			this.index = e.target.value;
		},
		// 保存添加平台信息
		formSubmit: function(e) {
			var Plans = e.detail.value.Plans;
			if (this.img == '') {
				uni.showToast({
					icon: 'none',
					title: '请上传截图！',
					duration: 2500,
				});
			} else if(this.UserNames == ''){
				uni.showToast({
					icon: 'none',
					title: '请填写用户名！',
					duration: 2500,
				});
			}else {
				uni.showLoading({
						title: "保存中....",
						mask: true
					}),
					uni.request({
						url: helper.websiteUrl + 'plan/planinfo',
						method: 'GET',
						data: helper.postdata({
							"type": 1,
							"userid": helper.getstate().userid,
							"Plans": Plans,
							"username": this.UserNames,
							"pics": this.img,
							"grade": this.grades,
							"state": this.state,
						}),
						success: res => {
							if (res.data.code == 200) {
								if (this.state == '1') {
									uni.showModal({
										title: '提示',
										content: "请等待客服人员审核",
										showCancel: false,
										confirmText: "确定",
										success: function(res) {
											if (res.confirm) {
												uni.navigateBack({
													delta: 1
												});
											}
										}
									});
								} else {
									uni.showModal({
										title: '提示',
										content: "保存成功",
										showCancel: false,
										confirmText: "确定",
										success: function(res) {
											if (res.confirm) {
												uni.navigateBack({
													delta: 1
												});
											}
										}
									});

								}

							} else {
								uni.showModal({
									title: '提示',
									content: res.data.info,
									showCancel: false,
									confirmText: "关闭",
								});
							}
							uni.hideLoading();
						},
						fail: () => {
							uni.hideLoading();
							uni.showToast({
								icon: 'none',
								title: '网络异常,请稍后重试'
							});
						}
					});
			}
		}
	}
}
